import { showNotification } from "@mantine/notifications";

/**
 *
 * @param {*} message  
 * @param {*} resultState   (success = green, warning = yellow, fail = red)
 */
const showToast = (message, resultState) => {
  return showNotification({
    message,
    autoClose: 2000,
    radius: "md",
    color: resultState,
  });
};

export default showToast;
