// npm package
import React, { Suspense } from "react";
import { Routes, Route, useLocation } from "react-router-dom";

// components
import {
  Nav,
  NavBack,
  Loading,
  KakaoRedirecting,
  NaverRedirecting,
} from "./components/ComponentIndex";

import {
  WebtoonPage,
  WebtoonDetail,
  BoardPage,
  BoardDetail,
  MyWebtoon,
  UserInfo,
  KakaoSignUp,
  NaverSignUp,
  NotFound,
} from "./pages/PagesIndex";

function App() {
  const { pathname } = useLocation();
  return (
    <>
      {pathname === "/webtoon" ||
      pathname === "/mywebtoon" ||
      pathname === "/board/detail" ||
      pathname === "/userinfo" ? (
        <NavBack />
      ) : (
        <Nav />
      )}
      <Suspense fallback={<Loading />}>
        <Routes>
          {/*   */}
          <Route exact path="/" element={<WebtoonPage />} />
          <Route exact path="/webtoon" element={<WebtoonDetail />} />

          {/*   */}
          <Route exact path="/board" element={<BoardPage />} />
          <Route exact path="/board/detail" element={<BoardDetail />} />

          {/*   */}
          <Route exact path="/userinfo" element={<UserInfo />} />

          {/*   */}
          <Route exact path="/mywebtoon" element={<MyWebtoon />} />

          {/*   */}
          <Route exact path="/all" element={<WebtoonPage />} />
          <Route exact path="/naver" element={<WebtoonPage />} />
          <Route exact path="/kakao" element={<WebtoonPage />} />
          <Route exact path="/kakaoPage" element={<WebtoonPage />} />

          {/*  */}
          <Route exact path="/regist/kakao" element={<KakaoSignUp />} />
          <Route exact path="/regist/naver" element={<NaverSignUp />} />

          {/*  */}
          <Route exact path="/kakaoLogin/callback" element={<KakaoRedirecting />} />
          <Route exact path="/naverLogin/callback" element={<NaverRedirecting />} />

          {/*    */}
          <Route path="*" element={<NotFound />} />
        </Routes>
      </Suspense>
    </>
  );
}

export default App;
